 4 سبتمبر\n                  "),_c('br'),_vm._v("أعلى 32 سيرفر : 11 سبتمبر\n                  "),_c('br'),_vm._v("أعلى 16 سيرفر : 18 سبتمبر\n                  "),_c('br'),_vm._v("أعلى 8 سيرفرات : 25 سبتمبر\n                  "),_c('br'),_vm._v("نصف نهائي البطولة : 2 أكتوبر\n                  "),_c('br'),_vm._v(" نهائي بطولة السيرفرات: 9 أكتوبر\n                  "),_c('br'),_vm._v("ملاحظة هامة: ربما يحدث تغيير في مواعيد البطولة وإذا حدث ذلك فسنقوم بإعلامكم في إعلانات وبريدات اللعبة وعبر الويشات وخدمة الدعم.\n              ")]),_c('p',[_vm._v("\n\n              * جوائز بطولة السيرفرات الكبرى *\n                      "),_c('br'),_vm._v("  1. تم تحسين جوائز تصنيف البطولة بشكل جنووووني خطيررر بحيث أن اللاعبون الثلاثة الذين حصلوا على المراكز الأولى في حدث (تصنيف اللاعبين المتميزين MVP ) من أعلى 4 سيرفرات في البطولة سيربحون الضابط الأخطر في عالم صقور العرب (الأرشيدوق) والذي سيمنحكم تعزيزات (زيادة دفاع كلي) بنسبة 55% وهجوم كلي ثابت 44% بالإضافة إلى الكثير من (أدوات الأشباح البرتقالية + صناديق تقنيات الضباط الخطيرة) بينما سيحصل باقي أعلى اللاعبين على الضابط الشرفي الخاص بالبطولة (ملك البطولات) وسيمنحكم تعزيزات (زيادة دفاع كلي) بنسبة 44% وهجوم كلي ثابت 36% بغض النظر عن مستوى الضابط لكن الدفاع الكلي متغير حسب مستواه وسيحصلون أيضا على الضباط الحمر الإستراتيجيين المميزين الجدد في اللعبة.\n                      "),_c('br'),_vm._v(" 2. زينة قاعدة شرفية (زينة برج العرب) دائمة ومؤقتة وستمنح صاحبها تعزيزات (زيادة هجوم كلي + زيادة دفاع كلي) بنسبة 10% وستكون من نصيب السيرفر البطل بالإضافة إلى (زينة قلعة التنين) دائمة ومؤقتة وستمنح صاحبها تعزيزات (زيادة دفاع كلي بنسبة 10% + زيادة سعة المسيرة 5% + خفض صحة العدو بنسبة 10% ) وأيضا لأعلى اللاعبين في السيرفر الثاني والثالث والرابع في البطولة واللاعب الأول في السيرفر الفائز بالبطولة سيحصل عليها بشكل دائم.\n                      "),_c('br'),_vm._v("  3. تم مضاعفة صناديق الهدايا الذهبية والفضية والبرونزية الأسطورية والتي تحتوي على كافة العناصر الهامة والجديدة في اللعبة مثل عناصر الأشباح المختلفة وموارد S5 و الألماس الأحمر وبكميات كبيرة من العن